@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}
body{
  background-color: rgb(40, 40, 40);
}
.homepage{
  display: flex;
}
.linka{
  color: white;
  transition: 2s;
}
.linka:hover{
  color: cornflowerblue;
}
@font-face {
    font-family: Platform;
    src: url(/static/media/Platform.f9b597c0.ttf);
}
#logo-img{
    width:141px;
}
#Appbar-logo{
    display: flex;
}
#outlined-helperText{
    display: none;
}
.Appbar{
    display: flex;
    background: black;
    position: fixed;
    width: 100vw;
    z-index: 2;
}
.icons{
    top: 28px;
    position: relative;
    color: white;
    padding-right: 39px;
    display: flex;
    margin-left: auto;
}
.icon-svg{
    padding: 0px 5px;
}
.icons:hover{
    cursor: pointer;
}
.Search-area{
    padding: 13px 21px;
}
.MuiInputBase-root.MuiOutlinedInput-root.MuiInputBase-formControl{
    background: white;
    height: 49px;
}
.resthumbnail{
    width: 35%;
    padding: 0px 11px;
}
.restitle{
    color: white;
    font-size: 15px;
    padding: 1px 13px;
}
.resinfo{
    color: white;
    font-size: 15px;
    padding: 1px 13px;
    font-family: 'Poppins';
}
.restname{
    padding: 6px 12px;
    font-size: 14px;
}
.MuiFormHelperText-root.MuiFormHelperText-contained{
    display: none;
}
.MuiInputBase-root.MuiOutlinedInput-root.MuiInputBase-formControl {
    font-family: Platform;
}
.thumbnail{
    width: 100%;
}
.info{
    display: flex;
    padding: 8px 18px;
}
.text-info{
    padding: 0px 16px;
}
.text-info {
    color: white;
    font-family: 'Poppins', sans-serif;
}
.video-name{
    width: 112%;
}
.videopage{
    display: none;
}
